import React, { Component } from 'react';
import App from './App';
import '../styles/appWrapper.css'
import {getPath, showErrorMessage} from '../helpers';

class AppWrapper extends Component {

    constructor(props) {
        super(props)
        this.state = {
            path: ''
        };
        this.updateConfig = this.updatePath.bind(this);
    }

    componentDidMount(){
        this.updatePath();
    }

    updatePath(){
        getPath().then((response) => {
            this.setState({
                path: response.path
            })
        }).catch(error => {
            showErrorMessage({message: error.response.data})
        });
    }

    render() {
        return (
            <div className="app-container" >
                {this.state.path.length > 0 ?
                 <App path={this.state.path}/>
                 :
                 <div className="no-path">Application path missing</div>
                }
            </div>
        )
    }
}

export default AppWrapper;


