import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../styles/App.css';
import folderIcon from './folder-icon.svg';
import {createApp, createService, openWorkspace, openDialogBox, showErrorMessage} from '../helpers';

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            name: '',
            description: '',
            path: this.props.path,
            projectType: 'app'
        };
        this.handleCreate = this.handleCreate.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.validateInputs = this.validateInputs.bind(this);
        this.handleBrowse = this.handleBrowse.bind(this);
        this.handleProjectChoice = this.handleProjectChoice.bind(this);
    }

    handleOnBlur = (property, value) => {
        this.setState(prevState => ({
            ...prevState,
            [property]: value
        }));
    };

    validateInputs() {
        const regexp = /^\S*$/; // a string consisting only of non-whitespaces
        if (this.state.name.match(regexp) != null && this.state.description.length > 0 && this.state.path.length > 0) return true;
        else return false
    }

    handleCreate() {
        if(this.validateInputs() === true) {
            const payload = {
                name: this.state.name,
                description: this.state.description,
                path: this.state.path,
            };
            if(this.state.projectType === 'service') {
                createService(payload).then(() => {
                    return openWorkspace({path: this.state.path, appName: this.state.name})
                }).catch(error => {
                    showErrorMessage({message: error.response.data})
                });
            }
            else {
                createApp(payload).then(() => {
                    return openWorkspace({path: this.state.path, appName: this.state.name})
                }).catch(error => {
                    showErrorMessage({message: error.response.data})
                });
            }
        } else {
            showErrorMessage({message: 'Inputs are not valid. Application name can not consist whitespaces'});
        }
    }

    handleBrowse() {
        openDialogBox().then((response) => {
            this.setState(prevState => ({
                ...prevState,
                path: response.data.fsPath,
            }))
        }).catch(error => {
            showErrorMessage({message: error.response.data})
        });
    }

    handleProjectChoice() {
        const e = document.getElementById("projectSelect");
        const selectedValue = e.options[e.selectedIndex].value;

        this.setState(prevState => ({
            ...prevState,
            projectType: selectedValue,
        }));
    }

    handleCancel() {
        console.log('Cancel');
    }

    render() {
        return (
            <div className="New-app-page">
                <div className="Page-header">New Cloud UX App</div>
                <div className="Page-section">
                    <div className="Section-content">
                        <ul>
                            <li>
                                <div className="Label-container">
                                    <label htmlFor="appName">Application Name</label>
                                </div>
                                <div className="Input-container">
                                    <input id="appName" onBlur={(event) => this.handleOnBlur('name', event.target.value)}/>
                                </div>
                            </li>
                            <li>
                                <div className="Label-container">
                                    <label htmlFor="appDescription">Description</label>
                                </div>
                                <div className="Input-container">
                                    <input id="appDescription" onBlur={(event) => this.handleOnBlur('description', event.target.value)}/>
                                </div>
                            </li>
                            <li>
                                <div className="Label-container">
                                    <label htmlFor="appDescription">Project type</label>
                                </div>
                                <div className="Input-container">
                                    <select id="projectSelect" className="Select" onChange={() => this.handleProjectChoice()}>
                                        <option value="app">App</option>
                                        <option value="service">Service</option>
                                    </select>
                                </div>
                            </li>
                            <li>
                                <div className="Label-container">
                                    <label htmlFor="appPath">Destination</label>
                                </div>
                                <div className="Input-container">
                                    <div className="Path-input-container">
                                        <input className="Path-input" id="appPath" value={this.state.path} onChange={(event) => this.handleOnBlur('path', event.target.value)}/>
                                        <img  className="folder-icon" src={folderIcon} alt="logo" onClick={() => this.handleBrowse()}/>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="Page-Footer">
                    <div className="Footer-Buttons-left">
                        {/* <button type="button" onClick={this.handleCancel}>Cancel</button> */}
                    </div>
                    <div className="Footer-Buttons-right">
                        <button type="button" onClick={this.handleCreate}>Create</button>
                    </div>
                </div>
            </div>
        );
    }
}

App.propTypes = {
    path: PropTypes.string.isRequired
};

export default App;



