import request, {GET, POST, API_URL} from './requests';
const qs = require('qs');

export const getPath = async () => {
    return await request(GET, API_URL + 'path/', {}, {
        headers: { "x-requested-with": 'app-creator-tool' }
    }).then(resp => Promise.resolve(resp.data));
};

export const createApp = (payload) => {
    return request(POST, API_URL + 'app/', qs.stringify(payload), {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};

export const createService = (payload) => {
    return request(POST, API_URL + 'service/', qs.stringify(payload), {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};

export const openWorkspace = (payload) => {
    return request(POST, API_URL + 'openWorkspace/', qs.stringify(payload), {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};

export const openDialogBox = () => {
    return request(GET, API_URL + 'openDialogBox/', {}, {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};

export const showErrorMessage = (message) => {
    return request(POST, API_URL + 'errorMessage/', qs.stringify(message), {
        headers: { "x-requested-with": 'app-creator-tool' }
    });
};



